import * as React from "react";
import { PureComponent } from "react";
import "./style.scss";
import { getAmmounts, getGlyphiconProps } from "./functions";
import Glyphicon from "../Glyphicon";
import AppContext from "../../context/AppContext";

class Kraj extends PureComponent<any> {
  static contextType = AppContext;

  handleClick = () => {
    const { toggleCountrySelection } = this.context;
    const { country } = this.props;
    toggleCountrySelection(country.id);
  };

  render() {
    const { country } = this.props;
    const { places, airports } = this.context;
    const glyphiconProps = getGlyphiconProps(country);
    return (
      <p className="Kraj">
        <button className="Kraj_button" onClick={this.handleClick}>
          <Glyphicon {...glyphiconProps} />
          {country.name}
        </button>{" "}
        {getAmmounts(country, places, airports)}
      </p>
    );
  }
}

export default Kraj;
