import * as React from "react";
import { PureComponent } from "react";
import "./style.scss";
import AppContext from "../../context/AppContext";
import StatystykiPodroze from "../StatystykiPodroze";
import StatystykiPrzyszle from "../StatystykiPrzyszle";
import StatystykiSkasowane from "../StatystykiSkasowane";

class Statystyki extends PureComponent {
  static contextType = AppContext;

  render() {
    const { modes } = this.context;
    const mode = modes.filter((mode: any) => mode.isSelected)[0].id;
    return (
      <div className="Statystyki">
        <div className="Statystyki_header">Statystyki</div>
        {mode === "past" && <StatystykiPodroze />}
        {mode === "future" && <StatystykiPrzyszle />}
        {mode === "canceled" && <StatystykiSkasowane />}
      </div>
    );
  }
}

export default Statystyki;
