export const getButtonClassName = (type: string, countries: any) => {
  const className = "Kraje_button--" + type;
  const isButtonHidden = countries.some(
    (country: any) =>
      (type === "show" && !country.isVisible) ||
      (type === "hide" && country.isVisible)
  );
  if (isButtonHidden) return className + "Hidden";
  return className;
};
