import * as React from "react";
import { PureComponent } from "react";
import "./style.scss";
import AppContext from "../../context/AppContext";
import Tryb from "../Tryb";

class Tryby extends PureComponent {
  static contextType = AppContext;

  render() {
    const { modes } = this.context;
    return (
      <div className="Tryby">
        {modes.map((mode: any) => (
          <Tryb key={mode.id} mode={mode} />
        ))}
      </div>
    );
  }
}

export default Tryby;
