export const getButtonClassName = (type: string, years: any) => {
  const className = "Lata_button--" + type;
  const isButtonHidden = years.some(
    (year: any) =>
      (type === "show" && !year.isVisible) ||
      (type === "hide" && year.isVisible)
  );
  if (isButtonHidden) return className + "Hidden";
  return className;
};
