import * as React from "react";
import { PureComponent } from "react";
import "./style.scss";
import { getAmmounts, getGlyphiconProps } from "./functions";
import Glyphicon from "../Glyphicon";
import AppContext from "../../context/AppContext";

class Warstwa extends PureComponent<any> {
  static contextType = AppContext;

  handleClick = () => {
    const { toggleLayerSelection } = this.context;
    const { layer } = this.props;
    toggleLayerSelection(layer.id);
  };

  render() {
    const { layer } = this.props;
    const { airports, places, routes } = this.context;
    const glyphiconProps = getGlyphiconProps(layer);
    return (
      <p className="Warstwa">
        <button className="Warstwa_button" onClick={this.handleClick}>
          <Glyphicon {...glyphiconProps} />
          {layer.name}
        </button>{" "}
        {getAmmounts(layer.id, { airports, places, routes })}
      </p>
    );
  }
}

export default Warstwa;
