import * as React from "react";
import { PureComponent } from "react";
import "./style.scss";
import OpisWyjazduMiejsce from "../OpisWyjazduMiejsce";

class OpisWyjazduMiejsca extends PureComponent<any> {
  render() {
    const { places } = this.props;
    if (places.length === 0) return null;
    return (
      <section className="OpisWyjazduMiejsca">
        {places.length > 1 ? "Miejsca" : "Miejsce"}:
        <ul className="OpisWyjazduMiejsca_list">
          {places.map((place: any) => (
            <li key={place}>
              <OpisWyjazduMiejsce id={place} />
            </li>
          ))}
        </ul>
      </section>
    );
  }
}

export default OpisWyjazduMiejsca;
