import * as React from "react";
import { PureComponent } from "react";
import AppContext from "../../context/AppContext";

class StatystykiPodroze extends PureComponent {
  static contextType = AppContext;

  render() {
    const {
      trips,
      countries,
      places,
      airports,
      routes,
      airlines,
      aircrafts
    } = this.context;
    const liczbaWyjazdow = trips.filter(
      (trip: any) => trip.hidden !== true && !trip.isFutureTrip
    ).length;
    const liczbaKrajow = countries.filter(
      (country: any) => country.isAvailable && country.isPastCountry
    ).length;
    const liczbaMiejsc = places.filter(
      (place: any) => place.isAvailable && place.isPastPlace
    ).length;
    const liczbaLotnisk = airports.filter(
      (airport: any) => airport.isAvailable && airport.isPastAirport
    ).length;
    const liczbaTras = routes.filter((route: any) => route.pastFlights > 0)
      .length;
    const liczbaLotow = routes.reduce(
      (subSum: any, route: any) => subSum + route.pastFlights,
      0
    );
    const filteredAirlines = airlines.filter(
      (airline: any) => airline.pastFlights > 0
    );
    const liczbaLinii = filteredAirlines.length;
    const linie = filteredAirlines.sort(
      (a: any, b: any) =>
        a.pastFlights === b.pastFlights
          ? a.name < b.name
            ? -1
            : 1
          : a.pastFlights > b.pastFlights
            ? -1
            : 1
    );
    const filteredAircrafts = aircrafts.filter(
      (aircraft: any) => aircraft.pastFlights > 0
    );
    const liczbaSamolotow = filteredAircrafts.length;
    const samoloty = filteredAircrafts.sort(
      (a: any, b: any) =>
        a.pastFlights === b.pastFlights
          ? a.name < b.name
            ? -1
            : 1
          : a.pastFlights < b.pastFlights
            ? 1
            : -1
    );
    return (
      <section>
        <p className="Statystyki_item">Wyjazdy: {liczbaWyjazdow}</p>
        <p className="Statystyki_item">Kraje: {liczbaKrajow}</p>
        <p className="Statystyki_item">Miejsca: {liczbaMiejsc}</p>
        <p className="Statystyki_item">Lotniska: {liczbaLotnisk}</p>
        <p className="Statystyki_item">Trasy lotnicze: {liczbaTras}</p>
        <p className="Statystyki_item">Loty: {liczbaLotow}</p>
        <p className="Statystyki_item">Linie lotnicze: {liczbaLinii}</p>
        <div className="Statystyki_item">
          <ul className="Statystyki_list">
            {linie.map((linia: any) => (
              <li key={linia.id}>
                {linia.name} ({linia.pastFlights})
              </li>
            ))}
          </ul>
        </div>
        <p className="Statystyki_item">Samoloty: {liczbaSamolotow}</p>
        <div className="Statystyki_item">
          <ul className="Statystyki_list">
            {samoloty.map((samolot: any) => (
              <li key={samolot.id}>
                {samolot.name} ({samolot.pastFlights})
              </li>
            ))}
          </ul>
        </div>
      </section>
    );
  }
}

export default StatystykiPodroze;
