import * as React from "react";
import { PureComponent } from "react";
import "./style.scss";
import { getGlyphiconProps, getAmmounts } from "./functions";
import Glyphicon from "../Glyphicon";
import AppContext from "../../context/AppContext";

class Rok extends PureComponent<any> {
  static contextType = AppContext;

  handleClick = () => {
    const { toggleYearSelection } = this.context;
    const { year } = this.props;
    toggleYearSelection(year.id);
  };

  render() {
    const { year } = this.props;
    const { trips } = this.context;
    const glyphiconProps = getGlyphiconProps(year);
    return (
      <p className="Rok">
        <button className="Rok_button" onClick={this.handleClick}>
          <Glyphicon {...glyphiconProps} />
          {year.id}
        </button>{" "}
        {getAmmounts(year, trips)}
      </p>
    );
  }
}

export default Rok;
