export const getButtonClassName = (type: string, trips: any) => {
  const className = "Wyjazdy_button--" + type;
  const isButtonHidden = trips.some(
    (trip: any) =>
      (type === "show" && !trip.isVisible) ||
      (type === "hide" && trip.isVisible)
  );
  if (isButtonHidden) return className + "Hidden";
  return className;
};
