import * as React from "react";
import { PureComponent } from "react";
import MapAirport from "../MapAirport";

class MapAirports extends PureComponent<any> {
  render() {
    const { airports } = this.props;
    return airports.map((airport: any) => (
      <MapAirport key={airport.id} airport={airport} />
    ));
  }
}

export default MapAirports;
