import * as React from "react";
import { PureComponent } from "react";
import "./style.scss";
import OpisWyjazduGaleria from "../OpisWyjazduGaleria";
import Glyphicon from "../Glyphicon";

class OpisWyjazduGalerie extends PureComponent<any> {
  render() {
    const { galleries } = this.props;
    if (!galleries || galleries.length === 0) return null;
    return (
      <section className="OpisWyjazduGalerie">
        <Glyphicon className="OpisWyjazduGalerie_icon" icon="photo_camera" />
        {galleries.length > 1 ? "Galerie" : "Galeria"}:
        <ul className="OpisWyjazduGalerie_list">
          {galleries.map((gallery: any) => (
            <li key={gallery.link}>
              <OpisWyjazduGaleria gallery={gallery} />
            </li>
          ))}
        </ul>
      </section>
    );
  }
}

export default OpisWyjazduGalerie;
