import * as React from "react";
import { PureComponent } from "react";
import "./style.scss";
import Glyphicon from "../Glyphicon";

class InfoWindowGalerie extends PureComponent<any> {
  render() {
    const { galleries } = this.props;
    if (!galleries || galleries.length === 0) return null;
    return (
      <section className="InfoWindowGalerie">
        <Glyphicon className="InfoWindowGalerie_icon" icon="photo_camera" />
        {galleries.length > 1 ? "Galerie" : "Galeria"}:{" "}
        <ul className="InfoWindowGalerie_list">
          {galleries.map((gallery: any) => (
            <li key={gallery.link}>
              <a
                href={gallery.link}
                target="_blank"
                rel="noopener noreferrer"
                title="Zobacz galeri"
                className="InfoWindowGalerie_link"
              >
                {gallery.tittle || gallery.source}
              </a>
            </li>
          ))}
        </ul>
      </section>
    );
  }
}

export default InfoWindowGalerie;
