import * as React from "react";
import { PureComponent } from "react";
import "./style.scss";
import AppContext from "../../context/AppContext";
import { getClassName } from "./functions";
import Menu from "../Menu";
import Wyjazdy from "../Wyjazdy";
import Filtry from "../Filtry";
import Statystyki from "../Statystyki";

class MenuContainer extends PureComponent {
  static contextType = AppContext;
  render() {
    const { isMenuShown, selectedMenu } = this.context;
    return (
      <div className={getClassName(isMenuShown)}>
        <Menu />
        {selectedMenu === "wyjazdy" && <Wyjazdy />}
        {selectedMenu === "filtry" && <Filtry />}
        {selectedMenu === "statystyki" && <Statystyki />}
      </div>
    );
  }
}

export default MenuContainer;
