import * as React from "react";
import { PureComponent } from "react";
import "./style.scss";
import AppContext from "../../context/AppContext";
import Glyphicon from "../Glyphicon";
import { getButtonClassName } from "./functions";
import Warstwa from "../Warstwa";

class Warstwy extends PureComponent {
  static contextType = AppContext;

  showAll = () => {
    const { showAllLayers } = this.context;
    showAllLayers();
  };

  hideAll = () => {
    const { hideAllLayers } = this.context;
    hideAllLayers();
  };

  render() {
    const { layers } = this.context;
    return (
      <div className="Warstwy">
        <div className="Warstwy_header">
          Warstwy
          <button
            className={getButtonClassName("show", layers)}
            onClick={this.showAll}
            title="Poka wszystkie warstwy"
          >
            <Glyphicon icon="visibility" className="Warstwy_icon" />
          </button>
          <button
            className={getButtonClassName("hide", layers)}
            onClick={this.hideAll}
            title="Ukryj wszystkie warstwy"
          >
            <Glyphicon icon="visibility_off" className="Warstwy_icon" />
          </button>
        </div>
        <div className="Warstwy_body">
          {layers.map((layer: any) => (
            <Warstwa key={layer.id} layer={layer} />
          ))}
        </div>
      </div>
    );
  }
}

export default Warstwy;
