import * as React from "react";
import { PureComponent } from "react";
import "./style.scss";
import AppContext from "../../context/AppContext";
import Glyphicon from "../Glyphicon";
import Rok from "../Rok";
import { getButtonClassName } from "./functions";

class Lata extends PureComponent {
  static contextType = AppContext;

  showAll = () => {
    const { showAllYears } = this.context;
    showAllYears();
  };

  hideAll = () => {
    const { hideAllYears } = this.context;
    hideAllYears();
  };

  render() {
    const { years } = this.context;
    return (
      <div className="Lata">
        <div className="Lata_header">
          Lata
          <button
            className={getButtonClassName("show", years)}
            onClick={this.showAll}
            title="Poka z wszystkich lat"
          >
            <Glyphicon icon="visibility" className="Lata_icon" />
          </button>
          <button
            className={getButtonClassName("hide", years)}
            onClick={this.hideAll}
            title="Ukryj z wszystkich lat"
          >
            <Glyphicon icon="visibility_off" className="Lata_icon" />
          </button>
        </div>
        <div className="Lata_body">
          {years
            .sort((a: any, b: any) => {
              return a.id < b.id ? 1 : -1;
            })
            .map((year: any) => (
              <Rok key={year.id} year={year} />
            ))}
        </div>
      </div>
    );
  }
}

export default Lata;
