import * as React from "react";
import { PureComponent } from "react";
import "./style.scss";
import OpisWyjazduRelacja from "../OpisWyjazduRelacja";
import Glyphicon from "../Glyphicon";

class OpisWyjazduRelacje extends PureComponent<any> {
  render() {
    const { reports } = this.props;
    if (!reports || reports.length === 0) return null;
    return (
      <section className="OpisWyjazduRelacje">
        <Glyphicon className="OpisWyjazduRelacje_icon" icon="import_contacts" />
        {reports.length > 1 ? "Relacje" : "Relacja"}:
        <ul className="OpisWyjazduRelacje_list">
          {reports.map((report: any) => (
            <li key={report.link}>
              <OpisWyjazduRelacja report={report} />
            </li>
          ))}
        </ul>
      </section>
    );
  }
}

export default OpisWyjazduRelacje;
